import React from 'react';
import Link from 'gatsby-link';
import Helmet from 'react-helmet';
import Img from 'gatsby-image';
import classNames from 'classNames';

import styles from '../styles/pages/contact.module.css';

const ContactPage = ({ data, windowHeight, windowWidth }) => (
  <div>
    <Img
      style={{
        zIndex: -1,
        position: "fixed",
        right: 0,
        bottom: 0,
        width: windowWidth,
        height: windowHeight,
      }}
      sizes={data.backgroundImage.sizes}
    />
    <Helmet
      title="Kontakt | Ingredienza"
      meta={[
        { name: 'description', content: 'Nehmen Sie Kontakt auf mit uns. Gerne beantworten wir Ihre Frage oder nehmen Bestellungen entgegen. Überzeugen Sie sich von unserem vielfältigen Angebot. Ingredienza – Ihre Teigwarenmanufaktur für frische Pasta für Ihren Gastronomiebetrieb  ► Kontaktieren Sie uns' },
        { name: 'keywords', content: 'Kontakt' }
      ]}
    />
    <h1>Kontakt</h1>

    <p>
      Haben Sie Fragen oder möchten Sie eine Bestellung aufgeben? Dann treten
      Sie mit uns in Kontakt. Sie können uns telefonisch, per E-Mail oder Fax
      erreichen.
    </p>

    Ingredienza<br />
    Die Teigwaren-Manufaktur GmbH <br />
    Rolf Thalheim<br />
    Güterstrasse 7<br />
    CH-3008 Bern<br />
    <div className={styles.infos}>
      <div className={classNames({
        [`${styles.info}`]: true,
        [`${styles.tel}`]: true,
      })}>
        Tel:
      </div>
      <div className={classNames({
        [`${styles.info}`]: true,
        [`${styles.fax}`]: true,
      })}>
        Fax:
      </div>
      <div className={classNames({
        [`${styles.info}`]: true,
        [`${styles.mail}`]: true,
      })}>
        Mail:
      </div>

      <div className={classNames({
        [`${styles.content}`]: true,
        [`${styles.tel}`]: true,
      })}>
        +41 (0)31 382 42 42
      </div>
      <div className={classNames({
        [`${styles.content}`]: true,
        [`${styles.fax}`]: true,
      })}>
        +41 (0)31 382 49 05
      </div>
      <div className={classNames({
        [`${styles.content}`]: true,
        [`${styles.mail}`]: true,
      })}>
        <a style={{ color: '#000' }}href="mailto:info@ingredienza.ch?subject=Kontakt%20Ingredienza.ch"> info@ingredienza.ch </a>
      </div>
    </div>
  </div>
)

export const query = graphql`
  query ContactImageQuery {
    backgroundImage: imageSharp(id: { regex: "/contactBg/" }) {
      sizes(maxWidth: 2300) {
        ...GatsbyImageSharpSizes
      }
    }
  }
`;

export default ContactPage;



